.home {
  display: flex;
  position: absolute;
  top: 545vh;
  left: 0;
  width: 98%; }

.home-grid {
  position: absolute;
  left: calc(50% - 162px);
  text-align: center;
  justify-content: center;
  z-index: 1; }

.banner-text h1 {
  font-size: 35px;
  color: #5a5959;
  font-family: "Work Sans";
  margin-bottom: 0.125em; }

.banner-text p {
  font-size: 15px;
  color: #5a5959;
  font-family: "Open Sans"; }

.banner-text br {
  content: "";
  margin: -1em;
  display: block;
  font-size: 20%; }

.social-links {
  display: flex;
  justify-content: center;
  align-items: center; }
  .social-links img {
    padding: 3px; }

.social-icon {
  height: 55px; }
